* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

body {
  font-size: 16px;
  background-color: #002347;
}

.weatherAppHeader {
  background-color: #002347;
  color: #FF8E00;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px solid #FF8E00;
}
.weatherAppHeader .weatherAppTitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.weatherAppHeader .weatherAppText {
  font-size: 1rem;
  font-style: italic;
}

.weatherDisplayContainer {
  height: auto;
  width: 100%;
  background-color: #002347;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #FF8E00;
  padding: 30px 0 30px 0;
}

.currentWeatherCard {
  display: flex;
  flex-direction: column;
  font-weight: bold;
  width: 300px;
  height: 400px;
  border: 3px solid #FF8E00;
  padding: 25px 0 25px 0;
  align-items: center;
}
.currentWeatherCard .locationContainer {
  display: flex;
  flex-direction: column;
  border-bottom: 3px solid #FF8E00;
  margin-top: 5px;
}
.currentWeatherCard .conditionsContainer {
  padding: 5px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}
.currentWeatherCard img {
  height: 175px;
  width: 175px;
}

footer {
  background-color: #FF8E00;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #002347;
  height: 50px;
  font-weight: bold;
  font-size: 1rem;
  margin-top: 10px;
}
footer a {
  text-decoration: none;
  color: inherit;
}
footer i {
  margin-left: 10px;
  font-size: 1.2rem;
}
footer .fa-spin-hover:hover {
  animation: fa-spin 0.4s ease-in-out;
}

.searchSelection {
  padding: 10px;
  color: #FF8E00;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 3px solid #FF8E00;
}
.searchSelection i {
  color: #FF8E00;
}
.searchSelection input {
  background-color: #002347 !important;
  color: #FF8E00 !important;
  border: 1px solid #FF8E00;
  width: 200px;
  font-weight: bold;
  font-size: 1rem;
}
.searchSelection input:focus {
  outline: none !important;
  border: 3px solid #FF8E00;
  background-color: #002347;
  color: #FF8E00;
}
.searchSelection ::placeholder {
  color: #FF8E00;
  font-weight: bold;
}
.searchSelection form {
  padding-top: 10px;
  display: flex;
  flex-direction: column;
}
.searchSelection button {
  background-color: #002347;
  margin-left: 5px;
  border: 1px solid #FF8E00;
}
.searchSelection .inputContainer {
  width: 100%;
}

.optionsSection {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 3px solid #FF8E00;
  padding: 10px 0 10px 0;
}
.optionsSection button {
  background-color: #FF8E00;
  color: #002347;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 5px;
  margin: 5px;
}

.control-next.control-arrow:before {
  border-left: 8px solid #FF8E00 !important;
  opacity: 1 !important;
}

.carousel .control-prev.control-arrow:before {
  border-right: 8px solid #FF8E00 !important;
  opacity: 1 !important;
}

.carousel .control-arrow, .carousel.carousel-slider .control-arrow {
  opacity: 1 !important;
}

.carousel .control-dots .dot {
  background-color: #FF8E00 !important;
}

